@import "./scss/_variable";

html {
     box-sizing: border-box;
     background-color: $activeClr;
     scroll-behavior: smooth;
     transition: 0.35s ease-out;
}
body {
     overflow-x: hidden;
}
* {
     margin: 0;
     padding: 0;
}
*,
*:before,
*:after {
     box-sizing: border-box;
}
input,
textarea,
select,
button {
     outline: none;
}
input {
     line-height: normal;
}
label,
button {
     cursor: pointer;
}
a {
     text-decoration: none;
}
img {
     display: block;
     max-width: 100%;
}
