@import "../../scss/_variable";

#memoryImg {
     margin-top: 70px;
}
.me__container {
     @include flex(center);
     flex-wrap: wrap;
}

.prev__img,
.next__img {
     border: none;
     font-size: 50px;
     color: $newMain;
     width: 65px;
     height: 65px;
     border-radius: 100rem;
     border: 5px solid $newMain;
     @media screen and (max-width: 767px) {
          margin-top: 350px;
     }
}
.me {
     &__full {
          position: relative;
          &--close {
               position: fixed;
               border: none;
               top: 60px;
               right: 200px;
               font-size: 50px;
               color: $newMain;
               width: 65px;
               height: 65px;
               border-radius: 100rem;
               border: 5px solid $newMain;
               @media screen and (max-width: 767px) {
                    opacity: 0;
               }
          }
          &--overlay {
               position: absolute;
               background-color: rgba(0, 70, 21, 0.842);
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               z-index: -2;
          }
          &--show {
               position: fixed;
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               z-index: 9999999;
               @include flex(between);
               display: none;

               padding: 0 200px;
               @media screen and (max-width: 767px) {
                    padding: 0 0px;
                    justify-content: center;
                    gap: 20px;
               }

               &-item {
                    @include abs(center);
                    min-width: 400px;
                    width: 800px;
                    height: 600px;
                    border: 5px solid $mainClr;
                    box-shadow: 0 0 10px $hoverClr;
                    z-index: -1;
                    @media screen and (max-width: 767px) {
                         width: 200px;
                         height: 400px;
                    }
               }
          }
     }
     &__box {
          min-width: 500px;
          height: 300px;
          background: $mainClr;
          position: relative;
          overflow: hidden;
          @media screen and (max-width: 767px) {
               min-width: 100%;
          }
          &:hover img {
               transform: scale(1.1);
          }
          & img {
               @include abs(full);
               cursor: zoom-in;
               transition: 0.5s ease-in;
          }
     }
}
